<template>
    <div>
        <Header></Header>
        <!-- <BreadCrumb></BreadCrumb> -->
        <Container v-bind:style="{'height':bodyHeight + 'px'}"></Container>
        <div id="dailog_mask" style="display:none"></div>
    </div>
</template>
<script>
import Header from './header'
// import BreadCrumb from './breadCrumb'
import Container from './container'
import {mapGetters} from 'vuex';

export default {
  data () {
    return {

    }
  },
  methods: {
  },
  components: {
      Header, Container
  },
  mounted() {
  },
  computed: {
      bodyHeight () {
          return document.documentElement.clientHeight;
      },
    ...mapGetters(['bodyHeight'])
  }
}
</script>
<style lang="scss" scoped>

</style>



