import * as types from '../mutations-types';
import { todoCount } from '@/utils/todocount'
const state = {
  count: 8,
  secondData: [],
  apiUrl:null,
  todoCount:{
    BusinessProcess:'',
    ContractSettlement:'',
    ExpenseReimbursement:''
  }
};
const getters = {
  count: state => state.count,
  secondData: state => state.secondData,
};
const mutations = {
  [types.GET_DATA](state, data) {
    state.secondData = data;
  },
  DECREMENT(state,data) {
    state.todoCount =data;
  },
};
const actions = {
  getSecondData({ commit }, data) {
    return this.$axios.post('url', data);
  },

  GETTODO(comtext){
      todoCount().then(response => {
        comtext.commit('DECREMENT',response.data.data)
      }).catch(

      );
  }
};
export default {
  state,
  getters,
  mutations,
  actions
}



