import { Loading } from 'element-ui';

/**
 * loading
 * @param { startLoading } 参数可以默认不写
 * @param { endLoading } 参数可以不写
 */
export default {
  load: {},
   /**
     * 启动loading
     * @param lock  load的开关(默认true)
     * @param text  显示提示title
     * @param background  lodaing的颜色
     * @param target  lodaing的加载区域
     */
  startLoading: (loadParam = {
    lock: true,
    text: '加载中……',
    background: 'rgba(255, 255, 255, 0.8)',
    target: document.querySelector('.loadingtext')
  }) => {
    this.load = Loading.service(loadParam);
  },
   /**
     * 关闭loading
     * @param time  loading关闭控制时间
     *
     */
  endLoading: (time) => {
    (!isNaN(time))?(typeof time==='string')?time=parseInt(time):time:(time=100);
    time ? setTimeout(() => {
      this.load.close();
    }, time) : this.load.close();
    
  }
}



