import * as types from '../mutations-types';
import { stat } from 'fs';
const state = {
  bodyHeight: document.documentElement.clientHeight,
  isIE: false,
  isSafari: false,
  todoCount: '',
  fullWidth: document.documentElement.clientWidth
}
const getters = {
  bodyHeight: state => state.bodyHeight,
  isIE: state => state.isIE,
  isSafari: state => state.isSafari,
  todoCount: state => state.todoCount,
  fullWidth: state => state.fullWidth
}
const mutations = {
  GET_BODYHEIGHT(state, val) {
    state.bodyHeight = Number(val);
  },
  GET_ISIE(state,val) {
    state.isIE = val
  },
  GET_ISSAFARI(state,val) {
    state.isSafari = val
  },
  // 获取待办数量
  GET_TODOCOUNT(state,val) {
    state.todoCount = val;
  },
  // 获取可视区宽度
  GET_FULLWIDTH(state,val) {
    state.fullWidth = val;
  }
}
const actions = {

}

export default {
  state,
  getters,
  mutations,
  actions
}



