                          </template>
                          <el-menu-item :index="childrenItem.path"  class="menuItem" v-for="(childrenItem,index) in item.children" :key="childrenItem.index">
                              <div class="itemContain">
                                <i :class="childrenItem.class"/>
                                <span>{{ childrenItem.name}}</span>
                              </div>
                              <el-badge class="mark listbadge" v-if="childrenItem.path==='/todo' && todoCount.BusinessProcess!='0'" :value="todoCount.BusinessProcess" />
                              <el-badge class="mark listbadge" v-if="childrenItem.path==='/bursement' && todoCount.ExpenseReimbursement!='0'" :value="todoCount.ExpenseReimbursement" />
                              <el-badge class="mark listbadge" v-if="childrenItem.path==='/contract' && todoCount.ContractSettlement!='0' " :value="todoCount.ContractSettlement" />
                          </el-menu-item>
                        </el-submenu>
                        <el-menu-item  v-else :index="item.path"  class="menuItem">
                          <div class="itemContain">
                            <i :class="item.class"/>
                            {{ item.name }}
                          </div>
                          <!--<el-badge class="mark listbadge" v-if="item.path==='/ccToMe' && todoCount.ContractSettlement!='0' " :value="todoCount.ContractSettlement" />-->
                        </el-menu-item>
                      </div>
                    </el-menu>
                 </div>
                <!--右侧容器高度占满--CQF-->
                <div class="contain-right" :span="20"  style="height:100%;">
                  <!-- header begin -->
                    <div class="personalinfo personalinfo-box">
                      <el-row class="info">
                        <div class="infoNext">
                          <div class="elCol2" >
                            <img src="../../assets/menu.png" alt="">
                            <a class="menuDft">菜单
                              <div class="menuList">
                                <div class="menuListop">
                                  <el-row>
                                    <el-col :xs="8" :sm="6" :md="6" :lg="6" :xl="6">
                                      <div class="bor-3">个人管理</div>
                                      <ul class="listmenu">
                                        <li>自定义组抄送</li>
