                                    </el-col>
                                    <el-col :xs="4" :sm="6" :md="6" :lg="6" :xl="6">
                                      <div class="bor-3">财务管理</div>
                                      <ul class="listmenu">
                                        <li>出纳付款管理</li>
                                        <li>会计凭证管理</li>
                                      </ul>
                                    </el-col>
                                    <el-col :xs="4" :sm="6" :md="6" :lg="6" :xl="6">
                                      <div class="bor-3">报表管理</div>
                                      <ul class="listmenu">
                                        <li>预算分析报表、台账</li>
                                        <li>流程效率报表</li>
                                      </ul>
                                    </el-col>
                                    <el-col :xs="8" :sm="6" :md="6" :lg="6" :xl="6">
                                      <div class="bor-3">报表管理</div>
                                      <ul class="listmenu">
                                        <li>返回旧版首页</li>
                                        <li>最老版后台</li>
                                        <li>新版后台</li>
                                        <li>供应商库管理</li>
                                        <li>采购系统管理</li>
