                          <div class="elCol2" >
                            <el-dropdown style="overflow: hidden; text-overflow: ellipsis;white-space: nowrap;margin-top: 7px;" class="top15" trigger="click" name="0">
                                  <span class="el-dropdown-link">
                                    <img style="float: left;" src="../../assets/smallperson.png" alt="">
                                    <span style="float: left; max-width: 101px;" class="userName" :title="userName">
                                      {{userName}}
                                    </span>
                                    <i style="float: left;" class="el-icon-arrow-down el-icon--right"></i>
                                  </span>
                              <el-dropdown-menu slot="dropdown" name="1">
                                <el-dropdown-item @click.native="burialPointEvent('个人设置');$router.push('/reminderSetting')">个人设置</el-dropdown-item>
                              </el-dropdown-menu>
                            </el-dropdown>
                          </div>
                          <!-- 身份切换 -->
