                    <div class="changeuser">
                        <el-dialog title="切换用户" :visible.sync="dialogFormVisible">
                        <el-form :model="form" class="">
                          <el-form-item label="切换用户名" label-width="100px">
                            <el-input v-model="form.username"></el-input>
                          </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                          <el-button @click="dialogFormVisible = false">取 消</el-button>
                          <el-button type="primary" @click="CommitUserChange" :disabled="!form.username.replace(/\s+/g, '')">确 定</el-button>
                        </div>
                      </el-dialog>
