        <!-- </el-container> -->

        </div>
</template>
<script>

import util from '../../js/util';
import { mapGetters } from 'vuex';
export default {
    name: "container",
    data() {
        return {
          showNone: false,
          selectMenuPath:'',
          menuPath:'',
          activeDate: '',
          version:util.version,
          //版本号 // src:'',头像
          userName:'',//登录名
          UserAccount:'',
          codeShow:false,//二维码初始不显示
          logoPhoto:require('../../../static/logo.png'),
          dialogFormVisible: false,
          IsAdmin: '',
          form: {
            username: ''
          }
        }
    },
    mounted() {
    //api/wechat/getImgByIDM
    // 参数：   adAccount
    // employeeNo
    // companyMail
    // 三个传一个就好了
    let that = this;
    that.$nextTick(()=>{
      that.$axios({
        url:'Home/GetCurrentUser',
        methods:'get'
      }).then((result) => {
        that.userName =result.data.data.UserDisplayName;
        that.UserAccount =result.data.data.UserAccount;
        that.IsAdmin = result.data.data.IsAdmin
        if(!result.data.data){
          that.userName = 'Admin'
        }
        localStorage.setItem('username',that.userName);
        localStorage.setItem('UserAccount',that.UserAccount);
      }).catch(err => {
        that.$message.error(`获取用户信息数据失败!ERROR MESSAGE:${err.message}`);
      });
    });
    var arr = this.$route.path.split('/');
    var path = '/' + arr[1];
    this.menuPath = path;
    this.selectMenuPath = path;
    this.seachProcessCount();
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.documentElement.clientWidth;
        window.screenHeight = document.documentElement.clientHeight;
        // that.fullWidth = window.screenWidth
        this.$store.commit('GET_FULLWIDTH',window.screenWidth);
        this.$store.commit('GET_BODYHEIGHT',window.screenHeight);
      })()
    },
      // 判断浏览器
      this.isBrower();
    //判断缓存数据中是不是管理员逻辑
    let manager = window.localStorage.getItem("loginname");
  },
    methods: {
      // 切换身份
        CommitUserChange() {
          const that = this
          this.$axios.post('/Account/SwitchLogin', {
              username: this.form.username,
              returnUrl: location.href
          }).then(res => {
            that.$message.success('切换成功')
            that.dialogFormVisible = false
            that.form.username = ''
            that.$axios({
              url:'Home/GetCurrentUser',
              methods:'get'
            }).then((result) => {
              that.userName =result.data.data.UserDisplayName;
              that.UserAccount =result.data.data.UserAccount;
              that.IsAdmin = result.data.data.IsAdmin
              if(!result.data.data){
                that.userName = 'Admin'
              }
              localStorage.setItem('username',that.userName);
              localStorage.setItem('UserAccount',that.UserAccount);
            }).catch(err => {
              that.$message.error(`获取用户信息数据失败!ERROR MESSAGE:${err.message}`);
            });
          }).catch(err => {
            that.$message.error('输入的用户不存在')
            that.dialogFormVisible = false
            that.form.username = ''
          })
        },
        //点击下载出现二维码
        codeDetails(){
          this.codeShow = true
        },
        hiddenDetail(){
        this.codeShow = false
        },
        ifTodo(path){
            return path==='/todo';
        },
        handleselect (val) {
            if(this.selectMenuPath === val){
            //    location.reload();
//            this.activeDate = new Date() + 1;
            }
            this.selectMenuPath = val;
        },
//        //获取我的代办数量
        seachProcessCount(){
            this.$axios.get('/Portal/WorkFlow/GetKStarWorklistCount').then((result) => {
              if(result.data.code===200){
                this.$store.dispatch('GETTODO')
              }
            }).catch(err=>{
              this.$message.error(`查询数据失败!ERROR MESSAGE:${err.message}`);
            })
        },
        //注销功能
        SignOut(){
            this.$axios.post('/Account/SignOut',{}).then(res=>{
              if(res.status===200){
                window.location.reload();
              }
            }).catch(err=>{

            })
        },
        isBrower () {
            var ua = navigator.userAgent;
            var isIE = ua.indexOf("compatible") > -1 && ua.indexOf("MSIE") > -1;
            if (isIE) {
                var reIE = new RegExp("MSIE (\\d+\\.\\d+);");
                reIE.test(ua);
                var fIEVersion = parseFloat(RegExp["$1"]);
                if(fIEVersion == 9 || fIEVersion == 10) {
                    this.$store.commit('GET_ISIE', true)
                }
            }
            var isSafari = ua.indexOf("Safari") > -1 && ua.indexOf("Chrome") == -1;
            if (isSafari) {
                this.$store.commit('GET_ISSAFARI', true)
            }
        },
    },
    watch:{
        menuPath () {
            var arr = this.$route.path.split('/');
            var path = '/' + arr[1];
            return path;
        }
    },
    computed: {
      todoCount(){
        return this.$store.state.initProgress.todoCount
      },
      fullHeight () {
        return  document.documentElement.clientHeight;
        },
      bodyHeight() {
        return  document.documentElement.clientHeight;
      },
      fullWidth () {
        return  document.documentElement.clientWidth;
      },
      menuList(){
        return this.$router.options.routes[0].children;
      },
//        ...mapGetters(
////          {
////            todoCount: 'todoCount'
////          }
////          ['todoCount','fullWidth', 'bodyHeight']
//        )
    }
}
</script>


<style lang="scss" scoped>
