            }
            //  后期会改为背景图片
         .el-menu-item.menuItem.is-active {
                font-weight: 600;
               border-radius: 5px;
                .itemContain {
                    color: #AC1F24;
                    font-size: 15px;
                    .iconfont{
                      color: #AC1F24;
                      font-weight: 600;
                      margin-right: 8px;
                    }
                }
              &:hover {
                color: #AC1F24;
                font-weight: 600;
                .itemContain {
                  background: none;
                }
              }
            }
          &::after{
         position: relative;
         z-index: 3;
         height: 100%;
         content: "";
         display: block;
         background: #FFFFFF;
         opacity: .93;
       }
     }

     .contain-right {
         width: 85%;
         text-align: left;
         overflow: hidden;
         height: calc(100% - 70px);
         padding: 0;
         margin: 0;
     }
 }
