<template>
  <div class="originater_dialog originator_dialog" style="-moz-user-select: none; -webkit-user-select: none;">
    <!--<div class="mask"></div>-->
    <div :class="[closeShow?'pc-candidates':'pc-candidates pc-background']">
      <div class="dialog_content" v-dialog-drag>
        <el-main
                 :class="closeShow ? '' : 'originator-dialog'"
                 :style="closeShow ? { 'padding': '0', 'overflow': 'hidden'} :{ 'position': 'absolute',
                                           'top': dialogTop + 'px',
                                           'left': dialogLeft + 'px' }">
          <div class="top" :class="closeShow ? '' : 'originator-dialog-title'" v-show="!closeShow">人员选择
            <span style="font-size: 30px;cursor: pointer;float:right;padding-right:5px" @click="close">×</span>
          </div>

          <el-tabs type="card"
                   @tab-click="handleClick"
                   v-loading="loading"
                   v-model="activeName"
                   element-loading-text="拼命加载中"
                   element-loading-background="rgba(255, 255, 255, 0.8)">
              <el-tab-pane name="first" label="收藏" icon="iconfont icon-member">

              </el-tab-pane>

              <el-tab-pane name="second" label="人员" icon="el-icon-time">

              </el-tab-pane>
            <el-tab-pane name="second" label="组织" icon="el-icon-time">

            </el-tab-pane>
            <el-tab-pane name="second" label="人员" icon="el-icon-time">

            </el-tab-pane>
            <el-tab-pane name="second" label="人员" icon="el-icon-time">

            </el-tab-pane>
            <el-tab-pane name="second" label="人员" icon="el-icon-time">

            </el-tab-pane>
            <el-tab-pane name="second" label="人员" icon="el-icon-time">

          </el-tab-pane>
              <div class="addrecently-box" v-bind:class="{ pullup: isActive }" >
                <div class="addrecently">
                  <div class="media">
                    <div class="pull"  @click="isActive = !isActive"></div>
                    <ul class="clearfix">
                    <div class="clearBtn" v-show="selectedPerson.length>0" @click="clerarUser"></div>
                      <li v-for="(person, index) in selectedPerson" :key="index">
                        <div class="media-body">
                          <i class="el-icon-circle-close"  @click="deleteUser(index)"></i>
                          <span class="media-heading" >{{  person.name }}</span>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="addrecently-footer">
                <!-- TO DO UI中有地址本功能，暂注释-->
                <!-- <a href="#" ><i class="iconfont icon-install"></i><span>设置我的地址本</span></a> -->
                <el-button type="primary" @click="confirmSelect" icon="el-icon-circle-check-outline" size="mini">确定</el-button>
                <el-button type="primary" @click="close" icon="el-icon-circle-close-outline" size="mini">取消</el-button>
              </div>
          </el-tabs>
        </el-main>
      </div>
    </div>

  </div>
</template>

<script>
import util from "../../js/util";

// axios cancel 取消重复请求
var source = null;
  export default {
    name: "",
    props:{
      childDataUrl:{
        type : Object,
        required : true
      },
      queryName:'',
      closeShow: false,
      oneCheck:false
    },
    watch: {
      organizationSearch(val) {
        // 搜索组织
        this.personnelList = []
        this.$refs.orgTree.filter(val);
      },
      peopleSearch(val){
        this.peopleSearch = val.replace(/\s+/g,"");
        if(val.trim()==''){
          this.personnelList = []
        }
        if( (!/^[\u3220-\uFA29]+$/.test(val) && val.length >= 2) || (/^[\u3220-\uFA29]+$/.test(val) && val.length >= 1) ){
          this.searchPeople();
        }
      }
    },
    created(){
      let that = this
      let selectedPerson = []
      //页面加载 处理回填的 选中数据
      if(that.childDataUrl.selectedPerson){
        for(let item of that.childDataUrl.selectedPerson){
          console.log(typeof item)
          //校验数据格式
          if(typeof item === 'object'){
            item.name = item.value
            let dialogperson = Object.assign({},item)
            selectedPerson.push(dialogperson)
          }
        }
        that.selectedPerson = selectedPerson
      }
    },
    mounted() {
      //获取最近联系人
      this.getLatelyContacts()
    },
    data() {
      return {
        dialogTop: (document.documentElement.clientHeight - 526) / 2,
        dialogLeft: (document.documentElement.clientWidth - 610) / 2,
        dialogVisible: true,
        // 已选择人员数组
        selectedPerson: [],
        //控制loading
        loading:false,
        //最近联系人数据
        people:[],
        // 最近选择的 全选状态
        historyChecked:false,
        // 最近选择的 多选状态
        isIndeter:false,
        // 默认选择 最近
        activeName:'first',
        //记录上次 tab name
        historyActiveTabName:'first',
        // 部门数据
        organizationData: [],
        //缓存部门数据
        cacheOrganizationData:{
          //在职
          onJob:[],
          //不在职
          notJob:[]
        },
        //面包屑 显示部门全路径
        bread: '',
        //离职人员状态
        checked:false,
        //人员搜索框 bind 值
        peopleSearch: '',
        // 人员 多选框 全选、反选 状态
        checkAll: false,
        // 人员 多选框 多选 状态
        isIndeterminate: false,
        //右侧人员数据
        personnelList: [],
        //部门全名 集合
        orgNames:[],
        //部门全名显示数据
        tooltipOrganName:'',
        //组织部门 tree
        defaultProps: {
          id:'orgID',
          children: 'm_ChildOrg',
          label: 'orgName'
        },
        //组织部门 tree  默认展开的节点 Id
        defaultKeys:[],
        // 组织部门 搜索框 值
        organizationSearch:'',
        //控制折叠
        isActive:false,
        // 当前页码
        pageIndex: 0,
        // 总条目数
        total: 0,
        // 每页显示 条目数
        pageSize: 5,
        //当前选择的 部门
        originatorUser: {
          orgID: '',
          orgName: ''
        }
      };
    },
    methods: {
      // 加载更多
      loadMore(){
        let that = this
        that.pageIndex++
         if (!that.checked) {
          // 获取 在职 组织 人员数据
          that.getUserPage(that.peopleSearch, that.originatorUser.orgID, '1',  that.originatorUser.orgName)
        }else{
          //获取 离职 组织人员 数据
          that.getleaveUserPage(that.originatorUser.orgID,that.peopleSearch,that.originatorUser.orgName)
        }
      },
      /**
       * 查询在职人员  加载更多 分页
       * @param name 用户名 查询条件
       * @param deptId  部门id
       * @param isWork  在职人员查询条件
       * @param linkName 部门全名
       */
      getUserPage(name,deptId,isWork,linkName){
        let that = this;
        that.loading = true
        //重置  人员的 勾选 状态
        that.checkAll = false
        that.isIndeterminate =false
        that.$axios.post( `/api/getUser?pageIndex=${that.pageIndex}&pageSize=${that.pageSize}`, {name:name,deptId:deptId,isWork:isWork}).then((result) => {
         //追加分页数据
         let list = that.handleToOnJobData(result.data.result,linkName)
          for(let item of list){
            that.personnelList.push(item)
          }
          //设置人员勾选状态
          that.handlePersonnelListChecked()
          that.loading = false;
        }).catch(err => {
          that.loading = false;
          that.$message.error(`查询在职人员数据失败!ERROR MESSAGE:${err}`);
        });
      },
      /**
       * 查询离职人员 加载更多 分页
       * @param orgId  部门id
       * @param name 用户名
       * @param linkName 部门全名
       */
      getleaveUserPage(orgId,name,linkName){
        let that = this;
        that.loading = true
        //重置  人员的 勾选 状态
        that.checkAll = false
        that.isIndeterminate =false
        that.$axios.post(`/api/getOutUser?pageIndex=${that.pageIndex}&pageSize=${that.pageSize}`,{orgId:orgId,name:name}).then(result =>{
          let list =  that.handleToNotJobData(result.data.result,linkName)
          for(let item of list){
            that.personnelList.push(item)
          }
          //设置人员勾选状态
          that.handlePersonnelListChecked()
          that.loading = false
        }).catch(err => {
          that.loading = false
          that.$message.error(`查询离职人员数据失败!ERROR MESSAGE:${err}`);
        });
      },
      //获取最近联系人
      getLatelyContacts(){
        let that = this
        that.loading = true
        //重置 勾选状态
        that.historyChecked = false
        that.isIndeter = false
        that.people = []
        that.$axios.post('/api/oftenUser/page',{}).then( (result) => {
          //处理数据
          that.people = that.handleLatelyContactsData(result.data.oftenUserList)
          //重新设置勾选状态
          that.setLatelyContactsCheck()
          that.loading = false
        }).catch(err => {
          that.loading = false
          that.$message.error(`查询数据失败!ERROR MESSAGE:${err}`);
        });
      },
      /**
        * 处理 最近联系人 数据
        * @param {Array} list 查询的数据
        * @returns {Array} 处理后的数据
        */
      handleLatelyContactsData(list){
        let that = this
        //获取所有 已经选择的 人员Id
        let historyUserIds = that.getSelectPersonUserId(that.selectedPerson);
        let data = []
        for(let it of list){
          let checked = false
          //筛选已经选中的人员
          if (historyUserIds.indexOf(it.oftenUsedAD)!=-1) {
            checked = true
            //删除已经存在的数据
            that.delSelectedPerson(it.oftenUsedAD);
          }
          if(it.displayName) {
            data.push({
              'name':it.displayName,
              'itemLink':`${it.deptId}-${it.companyName}`,
              'checked':checked,
              'userId':it.oftenUsedAD,
              'vankeId':it.oftenUsedVankeId
            })
          }
        }
        return data
      },
      //设置 最近 联系人 勾选状态
      setLatelyContactsCheck(){
        let that = this
        let count = 0;
        for (let it of that.people) {
          if (it.checked) {
            count++
          }
        }
        that.historyChecked =  count > 0 && count === that.people.length;
        that.isIndeter = count > 0 && count < that.people.length;
      },
      //清空最近联系人
      clearLinkman(){
        let that = this
        that.loading = true
        that.$axios.delete('api/oftenUser',{data:{}}).then( result => {
          //重置 勾选状态
          that.historyChecked = false
          that.isIndeter = false
          //清空数据
          that.people = []
          that.loading = false
        }).catch(err => {
          that.loading = false
          that.$message.error(`清空失败!ERROR MESSAGE:${err}`);
        });
      },
      //删除 已经存在的 最近联系人 (应为设置数据 checked = true 会自动添加进去 防止数据重复 )
      delSelectedPerson(userid){
        let that = this
        for (let i=0; i < that.selectedPerson.length; i++) {
          if (that.selectedPerson[i].userId === userid) {
            that.selectedPerson.splice(i,1);
          }
        }
      },
      //最近联系人 单选
      historyCheckeds(value){
        let that = this
        that.selectedPerson.splice(0,that.selectedPerson.length-1);
        that.selectedPerson[0] = value
      },
      //历史记录 全选、反选
      handleCheckAll(val){
        let that = this
        that.isIndeter = false;
        if(val){
          //全选
          for (var i = 0; i < this.people.length; i++) {
            var bool = false;
            for (var j = 0; j < this.selectedPerson.length; j++) {
              if (this.selectedPerson[j].userId === this.people[i].userId) {
                bool = true;
              }
            }
            if (!bool) {
              this.selectedPerson.push(this.people[i]);
            }
          }
        }else{
          //反选
          for (var i = 0; i < this.people.length; i++) {
            var bool = false;
            for (var j = 0; j < this.selectedPerson.length; j++) {
              if (this.selectedPerson[j].userId === this.people[i].userId) {
                bool = true;
                break;
              }
            }
            if (bool) {
              this.selectedPerson.splice(j,1)
            }
          }
        }
      },
      //单选双击赋值
      dbhistory(item){
        this.historyCheckeds(item);
        this.$emit('sureSubmit', {type:'originator',peoArr:this.selectedPerson})
      },
      //获取 已经选择的 人员Id
      getSelectPersonUserId(){
        let that =this;
        let arrUserId = [];
        for(let it of that.selectedPerson){
          arrUserId.push(it.userId);
        }
        return arrUserId;
      },
      /**
       * 查询 在职人员部门 数据
       * getTree
       */
      getTree(){
        let that = this
        that.loading = true;
        that.organizationData = []
        that.$axios.post(`/api/${that.childDataUrl.getTree}`, {'orgDepId':'A5A40ECE-0DBD-4F2B-89E8-6BA91B91EBF4'}).then((result) => {
          that.loading = false
          //记录缓存
          that.cacheOrganizationData.onJob = result.data.result
          //设置在职人员部门数据
          that.setOnJobTreeData(result.data.result)
        }).catch(err => {
          that.loading = false
          that.$message.error(`查询部门组织数据失败!ERROR MESSAGE:${err}`);
        });
      },
      /**
        * 设置在职人员部门数据
        * @param {Array} list 查询的数据
        */
      setOnJobTreeData(list){
        let that = this
        // 处理树数据
        that.organizationData = util.toTree(list,'orgID','parentOrgID','m_ChildOrg')
        //默认展开第一级
        that.defaultKeys[0] = that.organizationData[0].orgID
      },
      /**
        * 查询离职人员部门
        */
      dimissionDeid(){
        let that = this
        that.loading = true
        that.organizationData = []
        that.$axios.post('/api/getOrg',{}).then(res=>{
          that.loading = false
          //记录缓存
          that.cacheOrganizationData.notJob = res.data.result
          //设置 不在职人员部门数据
          that.setNotJobTreeData(res.data.result)
        }).catch(err => {
          that.loading = false
          that.$message.error(`查询部门组织数据失败!ERROR MESSAGE:${err}`);
        });
      },
      /**
        * 设置 不在职人员部门数据
        * @param {Array} list 查询的数据
        */
      setNotJobTreeData(list){
        let that = this
        let dimissList = []
        for(let item of list){
          dimissList.push({
            "orgID":item.orgid,
            "parentOrgID":item.parentOrgId,
            "orgName":item.orgName
          })
        }
        that.organizationData =  util.toTree(dimissList,'orgID','parentOrgID','m_ChildOrg')
        //默认展开第一级
        that.defaultKeys[0] = that.organizationData[0].orgID
      },
      //切换tab
      handleClick(tab) {
        let that = this
        switch(tab.name){
          //点击最近
          case "first":
            if(tab.name != that.historyActiveTabName){
              //获取最近联系人
              that.getLatelyContacts()
            }
            that.historyActiveTabName = "first"
          break;
          //点击人员
          case "second":
            if(tab.name != that.historyActiveTabName && that.organizationData.length == 0){
              //查询部门组织树的数据
              that.getTree()
            }
            // 重置人员选择 多选框状态
            that.checkAll = false
            that.isIndeterminate = false
            that.personnelList = []
            that.historyActiveTabName = "second"
          break;
        }
      },
      //新增 最近联系人
      addNewpeople(){
        let that = this
        let add = []
        for(let item of that.selectedPerson){
          //判断是否有vankeId字段
          if(item.vankeId){
            add.push(item.vankeId)
            //最多增加十个人员
            if(add.length >= 10){ break }
          }
        }
        //添加 最近选择
        that.$axios.post('api/oftenUser', {'oftenUsedVankeId':add}).then(result =>{
        })
      },
      //勾选在职人员
      onChangeUser() {
        let that = this
        // 重置人员选择 多选框状态
        that.checkAll = false
        that.isIndeterminate = false
        //清空部门搜索框
        that.organizationSearch = ''
        //清空人员数据
        that.personnelList = []
        if (!that.checked) {
          // 获取 在职 组织 人员数据
          that.onChangeUserToOnJob()
        }else{
          //获取 离职 组织人员 数据
          that.onChangeUserToNotJob()
        }
      },
       // 获取 在职 组织 人员数据
      onChangeUserToOnJob(){
         let that = this
        //存在 人员搜索条件
        if(that.peopleSearch.trim()!=''){
          that.getUser(that.peopleSearch,that.originatorUser.orgID,'1',that.originatorUser.orgName);
        }
        if(that.cacheOrganizationData.onJob.length == 0){
          //查询在职人员
          that.getTree()
        }else{
          //取缓存数据
          //设置在职人员部门数据
          that.setOnJobTreeData(that.cacheOrganizationData.onJob)
        }
      },
      //获取 离职 组织人员 数据
      onChangeUserToNotJob(){
         let that = this
        //存在 人员搜索条件
        if(that.peopleSearch.trim()!=''){
          //查询离职人员
          that.getleaveUser(that.originatorUser.orgID,that.peopleSearch,that.originatorUser.orgName)
        }
        if(that.cacheOrganizationData.notJob.length == 0){
          //查询离职人员
          that.dimissionDeid()
        }else{
          //取缓存数据
          //设置 不在职人员部门数据
          that.setNotJobTreeData(that.cacheOrganizationData.notJob)
        }
      },
      /**
        * 过滤组织选择
        */
      filterNode(value, data) {
        if (!value) return true;
        return data.orgName.indexOf(value) !== -1;
      },
      /**
       * 查询在职人员
       * @param name 用户名 查询条件
       * @param deptId  部门id
       * @param isWork  在职人员查询条件
       * @param linkName 部门全名
       */
      getUser(name,deptId,isWork,linkName){
        let that = this;
        that.loading = true
        //重置  人员的 勾选 状态
        that.checkAll = false
        that.isIndeterminate =false
        //重置分页的 数据
        that.pageIndex = 0  // 当前页码
        that.pageSize = 5   // 每页显示 条目数
        that.total = 0      // 总条目数
        //重置人员数据
        that.personnelList = [];
        if(source){ source.cancel('cancel');}
        source = that.$axios.CancelToken.source();
        that.$axios.post( `/api/getUser?pageIndex=0&pageSize=${that.pageSize}`,
          {name:name,deptId:deptId,isWork:isWork},
          { cancelToken: source.token}).then((result) => {
           //总条目数
          if(result.data.pageControllerInfo){
            that.total = result.data.pageControllerInfo.totalRecordCount
          }
          that.personnelList =  that.handleToOnJobData(result.data.result,linkName)
          //设置人员勾选状态
          that.handlePersonnelListChecked()
          that.loading = false;
        }).catch(err => {
          that.loading = false;
          if (that.$axios.isCancel(err)){
            // console.log('cancel',that.$axios.isCancel(err));
          }else{
            that.$message.error(`查询在职人员数据失败!ERROR MESSAGE:${err}`);
          }
        });
      },
      //处理 在职人员 数据
      handleToOnJobData(list,linkName){
        let that = this;
        let data = []
        for(let it of list){
            let itemLink = '';
            if(!linkName || linkName.trim() ==''){
              itemLink = (it.companyName)?`${it.companyName}-${it.orgNameFullPath}`:it.orgNameFullPath;
              if(!itemLink){itemLink = ''}
            }else{
              itemLink = linkName;
            }
            let itemLinkToop = itemLink;
            if(itemLink.length > 18){
              itemLink = itemLink.substr(0,18)+'...';
            }

            let checked = false;
            for (var i=0; i < that.selectedPerson.length; i++) {
              if (that.selectedPerson[i].userId === it.adAccount) {
                checked = true;
                that.selectedPerson.splice(i,1);
                break;
              }
            }
            data.push({
              'name':(it.displayName=='0' || it.displayName=='' || it.displayName==null)?it.userName:it.displayName,
              'itemLink':itemLink,
              'checked':checked,
              'itemLinkToop':itemLinkToop,
              'userId':it.adAccount,
              'accountType':it.accountType,
              'vankeId':it.vankeId
            })
        }
        //设置人员勾选状态
        that.handlePersonnelListChecked()
         return data
      },
      /**
       * 查询离职人员
       * @param orgId  部门id
       * @param name 用户名
       * @param linkName 部门全名
       */
      getleaveUser(orgId,name,linkName){
        let that = this;
        that.loading = true
        //重置  人员的 勾选 状态
        that.checkAll = false
        that.isIndeterminate =false
        //重置分页的 数据
        that.pageIndex = 0  // 当前页码
        that.pageSize = 5   // 每页显示 条目数
        that.total = 0      // 总条目数
         //重置人员数据
        that.personnelList = []
        that.$axios.post(`/api/getOutUser?pageIndex=0&pageSize=${that.pageSize}`,{orgId:orgId,name:name}).then(result =>{
          //总条目数
          if(result.data.pageControllerInfo){
             that.total = result.data.pageControllerInfo.totalRecordCount
          }
          that.personnelList = that.handleToNotJobData(result.data.result,linkName)
          //设置人员勾选状态
          that.handlePersonnelListChecked()
          that.loading = false
        }).catch(err => {
          that.loading = false
          that.$message.error(`查询离职人员数据失败!ERROR MESSAGE:${err}`);
        });
      },
       //处理 离职人员 数据
      handleToNotJobData(list,linkName){
        let that = this;
        let data = []
        for(let item of list){
          let userId = item.userId.slice(item.userId.indexOf("\\")+2,item.userId.length);
          let checked = false;
          for (var i=0; i < that.selectedPerson.length; i++) {
            if (that.selectedPerson[i].userId === userId) {
              checked = true;
              that.selectedPerson.splice(i,1);
              break;
            }
          }
          let itemLink = '';
          if(!linkName || linkName.trim() ==''){
            itemLink = item.orgName;
            if(!itemLink){itemLink = ''}
          }else{
            itemLink = linkName;
          }

          let itemLinkToop = itemLink;
          if(itemLink.length > 18){
            itemLink = itemLink.substr(0,18)+'...';
          }
          data.push({
            'name':item.name,
            'userId':userId,
            'checked':checked,
            'itemLink':itemLink,
            'itemLinkToop':itemLinkToop
          })
        }
        return data
      },
      //设置人员勾选状态
      handlePersonnelListChecked(){
        let that = this
        let count = 0
        //获取所有已经选中的人员Id
        let selectUserId = that.getSelectedPersonUserIds()
        for (let it of that.personnelList) {
          //右侧数据 checked = true || 已经选中的人员Id包涵
          if (it.checked || selectUserId.indexOf(it.userId) > -1) {
            count++
          }
        }
        that.checkAll = count > 0 && count === that.personnelList.length;
        that.isIndeterminate = count > 0 && count < that.personnelList.length;
      },
      // 搜索人员 模糊查询
      searchPeople () {
        let that = this;
        that.isIndeterminate = false;//checkbox恢复初始
        if (!that.checked) {
          //查询在职人员
          that.getUser(that.peopleSearch,that.originatorUser.orgID,'1',that.originatorUser.orgName);
        }else{
          //查询离职人员
          that.getleaveUser(that.originatorUser.orgID,that.peopleSearch,that.originatorUser.orgName)
        }
      },
      //关闭 弹窗
      close(){
        this.$emit('hideComponent')
      },
      // 全选/反选
      handleCheckAllChange(val) {
        this.isIndeterminate = false;
        if(val){
          for (var i = 0; i < this.personnelList.length; i++) {
            var bool = false;
            for (var j = 0; j < this.selectedPerson.length; j++) {
              if (this.selectedPerson[j].userId === this.personnelList[i].userId) {
                bool = true;
              }
            }
            if (!bool) {
              this.selectedPerson.push(this.personnelList[i]);
            }
          }
        }else{
          for (var i = 0; i < this.personnelList.length; i++) {
            var bool = false;
            for (var j = 0; j < this.selectedPerson.length; j++) {
              if (this.selectedPerson[j].userId === this.personnelList[i].userId) {
                bool = true;
                break;
              }
            }
            if (bool) {
              this.selectedPerson.splice(j,1)
            }
          }
        }
      },
      //人员选择  多选
      handleCheckedCitiesChange(val) {
        var count = this.reckonCount(this.personnelList);
        this.checkAll = count > 0 && count === this.personnelList.length;
        this.isIndeterminate = count > 0 && count < this.personnelList.length;
      },
      //历史记录单选
      historyCheckedlist(value){
        var count = this.reckonCount(this.people);
        this.historyChecked =  count > 0 && count === this.people.length;
        this.isIndeter = count > 0 && count < this.people.length;
      },
      //选择人员 单选
      clickPerson(value){
        this.selectedPerson.splice(0,this.selectedPerson.length);
        this.selectedPerson.push(value);
      },

      //单选 双击右侧列表直接选中赋值
      dblclickCheckedCitie(item){
        this.clickPerson(item);
        this.addNewpeople();
        this.$emit('sureSubmit', {type:'originator',peoArr:this.selectedPerson})
      },
      //删除人员
      deleteUser(index){
        let that = this
        that.selectedPerson.splice(index,1)
        //最近联系人
        let count = that.reckonCount(that.personnelList);
        that.checkAll =  count > 0 &&  count === that.personnelList.length;
        that.isIndeterminate = count > 0 && count < that.personnelList.length;
        //人员选择
        let counts = that.reckonCount(that.people);
        that.historyChecked = counts > 0 && counts === that.people.length;
        that.isIndeter = counts > 0 && counts < that.people.length;
      },
      //计算count
      reckonCount(list){
        var count = 0;
        var userIds = this.getSelectedPersonUserIds();
        for (let it of list) {
          if (userIds.indexOf(it.userId)>-1) {
            count++;
          }
        }
        return count;
      },
      //获取所有被选中人员ID
      getSelectedPersonUserIds(){
        var userIds = [];
        for(let it of this.selectedPerson){
          userIds.push(it.userId);
        }
        return userIds;
      },
      //清除所有勾选人员
      clerarUser(){
        this.selectedPerson.splice(0,this.selectedPerson.length);
        //重置 最近选择 跟人员的 勾选 状态
        this.isIndeter = false
        this.historyChecked = false
        this.checkAll = false;
        this.isIndeterminate =false;
      },
      // 选择左侧列表
      handleNodeClick(data,node) {
        let that = this;
        that.originatorUser.orgID = data.orgID;
        that.orgNames=[];
        that.getPrarentNode(node.parent);
        that.originatorUser.orgName = that.disposeOrganName(data.orgName);

        //获取所有上级节点
        if (!that.checked) {
          //查询在职人员
          that.getUser(that.peopleSearch,data.orgID,'1',that.originatorUser.orgName);
        }else{
          //查询离职人员
          that.getleaveUser(data.orgID,that.peopleSearch,that.originatorUser.orgName)
        }
      },
      /**
       * 处理部门全名称
       */
      disposeOrganName(orgName){
        let organName= '';
        //获取完整的组织结构名称
        for(let it of this.orgNames){
          organName += `${it.text}-`
        }
        organName += `${orgName}`;
        this.tooltipOrganName = organName;
        if(organName.length>27){
          this.bread = organName.substr(0,27)+'...';
        }else{
          this.bread = organName;
        }
        return organName;
      },
      //获取所有上级节点
      getPrarentNode(pNode) {
        if (pNode.data.orgName != undefined) {
          this.orgNames.unshift({text: `${pNode.data.orgName}`,petId:`${pNode.data.orgID}`});
        }
        if (pNode.parent != undefined) {
          this.getPrarentNode(pNode.parent);
        }
      },
      // 选择人员确定
      confirmSelect () {
        this.addNewpeople()
        this.$emit('sureSubmit', {type:'originator',peoArr:this.selectedPerson})
      }
    },
    computed: {
    }
  }
</script>

