      //-------------------------------------授权人 end
      //设置 提交表单数据
      setFormData(){
        let processIDs = [];
        let processNames = [];
        let processCategoryIDs = [];
        let checkedAll = '';
        let orgId = [];
        for (let it of this.processArrays) {
          processIDs.push(it.id); // 流程ID赋值
          processNames.push(it.name); // 流程name赋值
        }
        for (let it of this.processCategoryIDs) {
          processCategoryIDs.push(it.id); // 流程ID赋值
        }
        if (this.processCheckAll) {
          checkedAll = '0';
          for (let i = 0; i < this.processArrays.length; i++) {
            if(typeof this.processArrays[i].id === "string"){
              orgId.push(this.processArrays[i].id);
            }
          }
          processNames = [];
          processIDs = [];
          processCategoryIDs = [];
        }
        return {
          fromUserID: `VANKE\\${this.fromAuthorizingPerson[0].userId}`,    //授权人 Id
          fromUserName: this.fromAuthorizingPerson[0].value,   //授权人 名称
          toUserIDs:  `VANKE\\${this.toAuthorizingPerson[0].userId}`,    //被授权人 Id
          toUserNames: this.toAuthorizingPerson[0].value,   //被授权人 名称
          processNames: processNames,  //流程 名称 集合
          processIDs: processIDs,    //流程 ID 集合
          recType: this.userForm.recType,        //授权类型 默认 1
          beginTime: `${this.userForm.date[0]} 00:00:00`,     //发起时段 开始
          endTime: `${this.userForm.date[1]} 23:59:59`,       //发起时段 结束
          lotNumber: "",     //授权数据的 GUID号
          status: "0",       //状态 0-Active 1-Canceled 2-Expired
          userId: util.getLocalStorage("loginname"),        //当前登录人 Id
          operator: this.userForm.operator,      //操作人 名称
          operationDate: this.userForm.operationDate, //操作时间
          currentTime: moment(new Date()).format("YYYY-MM-DD HH:mm:ss"),   //当前时间
          description: "",    //描述
          checkedAll: checkedAll,
          orgId: orgId.join(','),
          processCategoryIDs: processCategoryIDs.join(',')
        }
      },
      //保存按钮
      btnSave() {
        util.burialPoint('授权设置-添加页面-保存');
        let that = this;
        this.getOftenIds(); // 获取常用流程选中的id
        // this.getCheckedKeys('', ''); // 选中的流程节点
        this.processArrays = this.unique(this.processArrays, 'id'); // 去除重复流程
        this.processCategoryIDs = this.unique(this.processCategoryIDs, 'id'); // 去除重复文件夹
        //校验输入数据
        //手动 设置下拉框选中值时 下拉框 v-model 无法获取
        if (!this.fromAuthorizingPerson || this.fromAuthorizingPerson.length ==0) {
          this.$message.warning("请选择授权人！");
          return;
        }
        if (!this.toAuthorizingPerson || this.toAuthorizingPerson.length ==0) {
          this.$message.warning("请选择被授权人！");
          return;
        }
        if (this.processArrays.length === 0 && this.processCategoryIDs.length === 0) {
          this.$message.warning("请选择授权流程！");
          return;
        }
        if (!this.userForm.date || this.userForm.date.length < 1) {
          this.$message.warning("发起时段不能为空");
          return;
        }
        if (that.userForm.date && that.userForm.date.length > 1) {
          if(that.userForm.date[0] < '1753-01-01' || that.userForm.date[1] < '1753-01-01') {
            this.$message.warning('请选择1753-01-01 ~ 9999-12-31之间的日期');
            // this.userForm.date = [];
            return;
          }
        }
        let form = this.setFormData();
        //判断是新增还是编辑
        if (this.params) {
          form.lotNumber = this.params.lotNumber;
          // 编辑
          this.editAuthor(form);
        } else {
          // 新增
          this.addAuthor(form);
        }
      },
      //新增保存用户授权
      addAuthor(form) {
        this.$axios.post("/api/UserAuthority", form).then(res => {
          if (res.data.result == 0) {
            this.$message.warning(res.data.messageList[0].message);
            return;
          } else if (res.data.messageList.length > 0) {
            this.$message.warning(res.data.messageList[0].message);
            return;
          } else {
            this.$message({
              type: "success",
              message: "保存成功!"
            });
            this.$router.push({path: "/authorized"});
          }
        }).catch(err => {
          this.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        });
        this.processArrays = [];
      },
     //编辑保存用户授权
      editAuthor(form) {
        this.$axios
          .post("/api/getUserAuthorityToUpdate", form)
          .then(res => {
            let result = res.data.result;
            if (result) {
              if (res.data.resultMessage != '') {
                this.$message.warning(res.data.messageList[0].message);
              } else {
                this.$message({
                  type: "success",
                  message: "保存成功!"
                });
                this.$router.push({path: "/authorized"});
              }
            } else {
              let msg = res.data.messageList[0].message;
              this.$message.warning(msg);
            }
          })
          .catch(err => {
            this.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
          });
        this.processArrays = [];
      },
      //获取常用流程选中的流程id
      getOftenIds() {
        this.commonProcessIDs = [];
        if (this.chekedIDs.length > 0) {
          for (let item of this.chekedIDs) {
            this.commonProcessIDs.push({'id': Number(item.processid), 'name': item.processName});
          }
        }
        this.processArrays = this.processArrays.concat(this.commonProcessIDs);
      },
      //取消
      btnCancle() {
         this.$router.push({path: "/authorized"});
       },
      // 判断选择文件夹后不选流程
      removeProcessIDs(data, checked) {
        if (checked && data.Child && data.processId === null) {
          let processIDs = [];
          for (let item of data.Child) {
            processIDs.push(item.id)
          }
          for (let i = 0; i < this.processArrays.length; i++) {
            if (processIDs.indexOf(this.processArrays[i].id) > -1) {
              this.processArrays.splice(i, 1);
              i--;
            }
          }
        }
      },
      //获取选中节点
      getCheckedKeys(data, checked) {
        let list = this.$refs.tree.getCheckedNodes();
        // 判断文件夹
        for (let i = 0; i < list.length; i++) {
          if (list.length > 0 && list[i].processId === null) {
            this.processCategoryIDs.push({
              "id": list[i].id,
              "name": list[i].name
            })
          }
        }
        // 判断流程
        for (let i = 0; i < list.length; i++) {
          if (list.length > 0 && list[i].processId !== null) {
            this.processArrays.push({
              "id": list[i].id,
              "name": list[i].name
            })
          }
        }
        // 合并全部流程和常用流程
        this.processArrays = this.processArrays.concat(this.commonProcessIDs);
        // 判断全选的选中状态
        let keys = this.$refs.tree.getCheckedKeys();
        if (keys[0] === this.organize.orgID) {
          this.processCheckAll = true;
          this.indeterminateProcess = false;
        } else if (keys.length === 0) {
          this.processCheckAll = false;
          this.indeterminateProcess = false;
        } else {
          this.processCheckAll = false;
          this.indeterminateProcess = true;
        }
        // 判断选择文件夹后不选流程
        setTimeout( () => {
          this.removeProcessIDs(data, checked);
        }, 100)
      },
      // 全部流程全选
      setCheckedAllNodes() {
        util.burialPoint('授权设置-添加页面-全部流程[全选]');
        this.processData = [];
        if (this.processCheckAll) {
          // for (let it of this.processTreeData) {
          //   if (it && it.hasOwnProperty('processId') && it.processId !== null && it.processId !== '0') {
          //     this.processData.push(it);
          //   }
          // }
          this.$refs.tree.setCheckedKeys([this.organize.orgID]);
        } else {
          this.$refs.tree.setCheckedKeys([]);
        }
      },
      toTree(data) {
        if (data == null || data.length == 0) return [];
        data.forEach(function (item) {
          delete item.Child;
        });

        var map = {};
        data.forEach(function (item) {
          map[item.id] = item;
        });

        var val = [];
        data.forEach(function (item) {
          var parent = map[item.pid];
          if (parent) {
            (parent.Child || (parent.Child = [])).push(item);
          } else {
            val.push(item);
          }
        });
        return val;
      },
      //设置 流程节点可选择
     /* disabledNode(data) {
        let nodeData = [];
        for (let it of data) {
          if (it) {
            if (it.hasOwnProperty("processId") && (it.processId == null || it.processId == '0')) {
              it.disabled = true;
            }
            nodeData.push(it);
          }
        }
        return nodeData;
      },*/
      //遍历数组，去掉不能选择的流程节点（isUpgrade=0）
      deleteNode(data) {
        for (let i in data) {
          if (data[i].isUpgrade == "0") {
            delete data[i];
          }
        }
        return data;
      },
      //数组除重复
      unique(list, key) {
        var arr = [];
        for (var i = 0; i < list.length; i++) {
          if (i == 0) {
            arr.push(list[i]);
          }
          let b = false;
          if (arr.length > 0 && i > 0) {
            for (var j = 0; j < arr.length; j++) {
              if (arr[j][key] == list[i][key]) {
                b = true;
              }
            }
            if (!b) {
              arr.push(list[i]);
            }
          }
        }
        return arr;
      }
    }
  };
</script>

